<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <b-table bordered responsive hover :items="sequenceLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="show_details" slot-scope="row">
          <b-link @click="row.item._showDetails=!row.item._showDetails" v-show="row.item.plasm_status!=='待血浆分离'">{{row.item._showDetails?'收起':'展开'}}</b-link>
        </template>
        <!-- 详情模板 -->
        <template slot="row-details" slot-scope="row">
          <b-card>
            <b-row class="mb-2">
              <!--<b-col sm="3">文库上机体积：</b-col>
              <b-col sm="3">{{ row.item.libseq_volume?row.item.libseq_volume+row.item.libseq_volume_unit:'' }}</b-col>
              <b-col sm="3">文库上机浓度：</b-col>
              <b-col sm="3">{{ row.item.libseq_conc?row.item.libseq_conc+row.item.libseq_conc_unit:'' }}</b-col>-->
              <b-col sm="3">测序文件1存储位置：</b-col>
              <b-col sm="9">{{ row.item.seq_file1_path }}</b-col>
              <b-col sm="3">测序文件2存储位置：</b-col>
              <b-col sm="9">{{ row.item.seq_file2_path }}</b-col>
              <!-- <b-col sm="3">测序Run编号：</b-col>
              <b-col sm="3">{{ row.item.seq_run_id }}</b-col> -->
              <b-col sm="3">最后更新时间：</b-col>
              <b-col sm="3">{{ row.item.updated_at }}</b-col>
            </b-row>
          </b-card>
        </template>
        <template slot="vip" slot-scope="row">
          <span v-show="row.item.vip==='1'" style="color:red">VIP</span>
        </template>
        <template slot="product_number" slot-scope="row">
          <span v-if="row.item.product_number==='J53D-1'">绿卡(J53D-1)</span>
          <span v-if="row.item.product_number==='J53D-2'">黑卡(J53D-2)</span>
        </template>
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="browSeq(row)" v-show="(!row.item.seq_file1_path||!row.item.seq_file2_path)&&(permissionList.indexOf('19')>-1)">
           测序配置
          </b-button>
          <b-button size="sm" class="mr-2" variant="info" @click="joinSeqQueue(row)" v-show="row.item.seq_file1_path&&row.item.seq_file1_status==='上传成功'&&row.item.seq_file2_path&&row.item.seq_file2_status==='上传成功'&&row.item.in_list==='未添加队列'&&!row.item.seq_status&&(permissionList.indexOf('21')>-1)">
           加入测序队列
          </b-button>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
// import uploadBigFile from '../common/UploadBigFile';
  export default{
    components:{
      // uploadBigFile
    },
    mounted(){
      // 判断当前用户的权限,并转成数组
      if(this.$cookies.get("permissionvue")){
        this.permissionList = this.$cookies.get("permissionvue").split(',')
        // console.log(this.permissionList)
      }
      
      this.getTableData()
    },
    data () {
      return {
        menus: [{
          text: '测序文件管理',
          href: '#'
        }],
        permissionList:[],
        fields: {
          show_details:{
            label:'详情'
          },
          order_id:{
            label:'申请单号'
          },
          name:{
            label:'受检者姓名'
          },
          product:{
            label:'产品名'
          },
          vip:{
            label:'VIP'
          },
          product_number:{
            label:'货号'
          },
          lib_name:{
            label:'文库编号'
          },
          seq_file1_status:{
            label:'测序文件1状态'
          },
          seq_file2_status:{
            label:'测序文件2状态'
          },
          in_list:{
            label:'测序队列'
          },
          operate:{
            label:'操作'
          }
        },
        tableEmptyText:'正在请求数据...',
        sequenceLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/libseq/list",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data)
          this.sequenceLists=response.data.data
          this.sequenceLists.forEach(item=>{
            this.$set(item,'_showDetails',false)
          })
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      browSeq(row){
        window.open("/#/browsequence?libseqid=" + row.item.libseqid,'_blank')
      },
      joinSeqQueue(row){
        this.$Loading.start()
        this.$Spin.show({
          render: (h) => {
              return h('div', [
                  h('Icon', {
                      'class': 'spin-icon-load',
                      props: {
                        type: 'load-c',
                        size: 40
                      }
                  }),
                  h('div', '正在加入测序队列，请稍候！')
              ])
          }
        })
        this.$http.post("/api/Analysis/pipeline",qs.stringify({order_id:row.item.order_id})).then(response=>{
          this.getTableData()
          this.$Spin.hide();
          if(response.data.success){
            this.$Message.success({content: '加入测序队列成功！'})
          }
          if(response.data.error){
            this.$Message.error({content: response.data.error})
          }
        }).catch(err=>{
          this.$Spin.hide();
          this.$Message.error({content: '加入测序队列失败！请稍候重试'})
        })
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
#plasm_use_unit{
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
#plasm_unit{
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

</style>


